.form-header {
	display: flex;
	justify-content: flex-end;
	min-height: 40px;
	cursor: move;
}
.close-form {
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	transition: all 0.5s ease;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	font-size: 1rem;
	cursor: pointer;
	border: none;
	color: var(--BrightRed);
	&:hover {
		filter: brightness(1.25);
	}
}

.wrapper {
	position: absolute;bottom: 0;
	display: flex;
	flex-direction: column;
	max-width: 620px;
	min-width: 320px;
	height: 230px;
	padding: 8px 16px;
	border: 2px solid var(--border-color);
	border-radius: 6px;
	background-color: var(--background-sec);
	overflow: auto;
	z-index: 999;
	form {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		textarea{flex-grow: 1;}
		.form-btn-group {
			gap: 5px;
			display: flex;
			justify-content: flex-end;
			width: 100%;
			margin-top: 5px;
		}

		.btnSubmit {
			color: var(--LimeGreen);

			&:hover {
				border-color: var(--LimeGreen);
			}

			&[tabindex]:focus {
				outline-color: var(--LimeGreen);
			}
		}

		.btnReset {
			color: var(--BrightRed);

			&:hover {
				border-color: var(--BrightRed);
			}

			&[tabindex]:focus {
				outline-color: var(--BrightRed);
			}

			padding: 0 12px;
			flex-grow: 0;
		}

		button {
			padding: 6px 12px;
			transition: color 0.5s ease;
			background-color: transparent;
			font-size: 1.25rem;
			cursor: pointer;
			border: 2px solid var(--border-color);
			border-radius: 6px;
			color: inherit;

			&:disabled {
				pointer-events: none;
				opacity: 0.35;
				cursor: not-allowed;
			}

			&:active {
				border-color: red;
			}
		}
	}
}

@media screen and (min-width: 920px) {
	.wrapper {
		form {
			.form-btn-group {
				margin-top: 5px;
			}

			button {
				height: unset;
				margin-left: 0;
				margin-top: 5px;
				padding: 8px;
			}
		}
	}
}