
.float-form-wrapper{
   position: fixed;  
   left: 16px;
   bottom: 16px;
    border:8px;
    border: 2px solid var(--border-color);
    background-color: var(--background-sec);
    overflow: hidden;
}
.float-form-content{
    width: 100%;
    max-width: 620px; 
    padding: 8px 16px;
}

.first-wrapper{
   position: fixed;
   top: 50%;
   left: 50%;
   transform: translate(-50% ,-50%);
}
.header{
    cursor: move;
    padding: 15px;
}
.wrapper{
    width: 100%;
    max-width: 320px;
    height: 320px;
    border:8px;
    border: 2px solid var(--border-color);
    background-color: var(--background-sec);
    overflow: hidden;
}
.wrapper-content{
    padding: 0 16px 16px;
    height: calc(100% - 25px);
    overflow: overlay;
    user-select: none;
}
.header{
    top: 0;
    left: 0;
    position: sticky;
    display: flex;
    justify-content: flex-end;
    min-height: 25px;
}
.close-edit-menu{
    transition: all 0.5s ease; 
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
	font-size: 1rem;
	cursor: pointer;
    border: none;
	color: var(--BrightRed);
    &:hover{
        filter: brightness(1.25);
    }
}
.section_top{
    border-bottom: 2px solid var(--border-color);
    padding: 0 0 16px 0;
}
.section_styles{
    padding: 16px 0;
}
.props-input{
    max-width: 125px;
}
.text_input{
    user-select: none;
     -webkit-appearance: none;
	appearance: none;
	background-color: var(--background-pri);
    width: 100%;
    padding: 15px;
	border: 2px solid var(--border-color);
	color: inherit;
	font-size: 1.25rem;
    cursor: text;
}

.section_heading{
    font-weight: 700;
    text-transform: capitalize;
    padding: 8px 0px;
}
.flex-col{
    display: flex;
    flex-direction: column;
    padding-left: 8px;
}

.flex-row{
       display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    padding: 8px;
}
.btn-remove-element{
     -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    cursor: pointer;
    transition: color 250ms ease;
    color: var(--text-color);
    &:hover{
        color: red;
    }
}
.btn-group{
    display: flex;
    gap: 5px;
    justify-content: flex-end;
    button{
   -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    display: block;
    color: var(--text-color);
    font-size: 1.25rem;
    cursor: pointer;
    }
    .reset-btn{
         &:hover{
            color: var(--BrightRed);
        border-color: var(--BrightRed);
    }
    }
   .add-btn{
     &:hover{
        color: var(--LimeGreen);
        border-color: var(--LimeGreen);
    }
} 
}

.group-btns{
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    .tag-btn{
     -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    cursor: pointer;
    transition: color 250ms ease;
    color: var(--text-color);
    &:active{
        color: red;
    }
    &:hover{
        color: var(--LimeGreen);
        border-color: var(--LimeGreen);
    }
}
}

.row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 15px;
    padding: 8px;
    &>input{
    -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 3px;
	border: none;
	border: 1px dashed var(--border-color);
	color: var(--text-color);
	font-size: 1rem;
    cursor: default;
}
}
.selecedElement{
    outline:1px dashed var(--BrightRed);
    outline-offset: 8px;
}

.select-wrapper{
    -webkit-appearance: none;
	appearance: none;
    background-color: transparent;
    border: 2px solid var(--border-color);
    border-radius: 3px;
    padding: 3px 6px;
    font-size: 1.25rem;
    color: inherit;
    cursor: pointer;
}
.option{
    -webkit-appearance: none;
	appearance: none;
    padding: 3px 6px;
    font-size: 1.25rem;
    color: inherit;
    background-color: transparent;
    &:hover{
        background-color: var(--background-sec);
    }
}


.buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
}
.checkbox-wrapper{
        border: 1px solid var(--border-color);
        border-radius: 3px;
        padding: 8px 12px;
        input{display: none;}
        input:checked + label{
            color: var(--LimeGreen);
        }
       label{
        cursor: pointer;
       } 
        &:hover{
        color: var(--LimeGreen);
        border-color: var(--LimeGreen);
    }
}