
.App-viewer {
	flex-grow: 1;
	padding: 15px;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	gap: 15px;
	transition: all 0.5s ease;
	overflow: overlay;
}

.wrapper{
	flex-grow: 1;
	height: 100%;
	overflow: overlay;

}
.main{
	position: relative;
	display: flex;
	height: 100%;
	padding: 25px 15px;
	background-color: var(--background-pri);
	overflow: overlay;
}
.main-content{
	padding:15px;
	flex-grow: 1;
	height: 100%;
	overflow: overlay;
	user-select: none;
}


.right-panel{position: relative;}
.viewer-controller-btns{
	display: flex;
	flex-direction: column;
	gap: 8px;
	justify-content: flex-end;
	padding: 8px 12px;
	button{
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		font-size: 1.25rem;
		padding: 3px 6px;
		border-radius: 2px;
		border: 1px solid var(--border-color);
		color: var(--text-color);
		transition: color 250ms linear;

		cursor: pointer;
		
	}
	.show-input-btn{
		&:hover{
			color: var(--LimeGreen);
			border-color: var(--LimeGreen);
		}
	}
	.speaker-btn{
		&:hover{
			color: var(--LimeGreen);
			border-color: var(--LimeGreen);
		}
	}
}